<template>
  <div class="">
    <b-row class="">
      <b-col cols="12">
        <b-row>
          <b-col cols="5">
            <label class="mb-2 mt-3" for="inline-form-input-name">User Key</label>
            <input type="text" v-model="user_key" class="form-control mx-0" rows="15" placeholder="Enter User Key" @input="userKey()" />
          </b-col>
          <b-col cols="5">
            <label class="mb-2 mt-3" for="inline-form-input-name">Params</label>
            <textarea class="form-control mx-0" placeholder="Enter Params" v-model="params" @input="userKey()"></textarea>
          </b-col>
          <b-col cols="2" class="mt-5">
            <button @click="loadWidgets" size="sm" class="btn btn-block btn-primary mb-3 px-5">Try it</button>
          </b-col>
        </b-row>
        <b-row>
          <b-col cols="7">
            <label class="mb-2 mt-3" for="inline-form-input-name">Script</label>
            <b-card>
              <b-form>
                <textarea disabled v-model="code" class="form-control mx-0" rows="15"> </textarea>
              </b-form>
            </b-card>
          </b-col>
          <b-col cols="5">
            <div id="_500plugins_widget" style="border-left: 1px solid #f1f1f1;font-family: 'Roboto', sans-serif;flex: 0 0 25%;max-width: 35%;margin-top:10px"></div>
          </b-col>
        </b-row>
      </b-col>
    </b-row>
  </div>
</template>
<script>
  export default {
    name: "SSO",
    data() {
      return {
        user_key: "",
        code: "",
        params: "{}",
      };
    },
    props: {
      api_key: String,
    },
    mounted() {
      this.code = `window._AppletIO.getWidget().render('_500plugins_widget','{{user_key}}', {{params}})`;
    },
    methods: {
      loadWidgets() {
        let params = JSON.parse(this.params);
        window._AppletIO.getWidget().render("_500plugins_widget", this.user_key, params);
      },
      userKey() {
        this.code = `window._AppletIO.getWidget().render("_500plugins_widget","${this.user_key}",${this.params})`;
      },
    },
    watch: {
      api_key() {
        this.loadWidgets();
      },
    },
  };
</script>
<style scoped></style>
