<template>
  <div class="">
    <div class="row">
      <div class="col-md-9">
        <select class="form-control mx-2" aria-label="Default select example" v-model="app" @change="code = `window._AppletIO.getOAuth().authenticate('${app}')`">
          <option value="" selected>Select app</option>
          <option v-for="item in apps" :key="item">{{ item }}</option>
        </select>
      </div>
      <div class="col-md-3">
        <button size="sm" class="btn btn-block btn-primary mb-3 px-5" @click="authenticate()">Try it</button>
      </div>
    </div>
    <div class="row">
      <div class="col-md-7">
        <label class="mb-2 mt-3" for="inline-form-input-name">Script</label>
        <b-card>
          <b-form>
            <textarea disabled v-model="code" class="form-control mx-3" rows="15"> </textarea>
          </b-form>
        </b-card>
      </div>
      <div class="col-md-5">
        <label class="mb-2 mt-3" for="inline-form-input-name">Result</label>
        <textarea disabled :value="response" class="form-control mx-3" rows="15"></textarea>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "OAuth",
    data() {
      return {
        app: "",
        apps: [],
        code: "",
        response: "",
      };
    },
    props: {
      api_key: String,
    },
    mounted() {
      this.code = `window._AppletIO.getOAuth().authenticate('${this.app}')`;
      this.apps = window._AppletIO
        .getApps()
        .filter((i) => i.api.includes("client_id") || i.api.includes("consumer_key"))
        .map((i) => i.name);
    },
    methods: {
      authenticate() {
        if (this.app) {
          window._AppletIO
            .getOAuth()
            .authenticate(this.app)
            .then((response) => {
              this.response = JSON.stringify(response.data);
            });
        }
      },
    },
  };
</script>
<style scoped></style>
